	// #endif

	// #ifdef MP-WEIXIN
	import Aegis from 'aegis-mp-sdk';
	// #endif

	// #ifdef H5
	import Aegis from 'aegis-web-sdk';
	// #endif

	import logger from './utils/logger'; // app.js
	import {
		genTestUserSig
	} from './debug/GenerateTestUserSig.js';

	const aegis = new Aegis({
		id: 'iHWefAYqKznuxWjLnr', // 项目key
		reportApiSpeed: true, // 接口测速
	});
	uni.$aegis = aegis
	// 首先需要通过 uni.requireNativePlugin("ModuleName") 获取 module

	export default {
		globalData: {
			windowWidth: 0,
			windowHeight: 0,
			amapWebKey: "31869e8d1c25cd86dd128861e9c657c5",
			// #ifdef APP-PLUS
			amapKey: "35d86249652912cfd2749439829ec1ba",
			// #endif
			// #ifdef MP-WEIXIN
			amapKey: "35d86249652912cfd2749439829ec1ba",
			// #endif
			statusBarHeight: 0,
			screenHeight: 0,
			pageHeight: 0,
			safeArea: '',
			safeAreaBottom: 0,
			top: 0,
			menuButtonWidth: 0,
			menuButtonLeft: 0,
			menuButtonHeight: 0,
			navHeight: 0,
			// weburl: 'https://taketest.sdwanyue.com/',
			weburl: 'https://take.sdwanyue.com/',
			version: '1.2.0',
			platform: '',
			openid: '',
			service_url_kefu: '',
			SDKAppID: genTestUserSig('').sdkAppID,
			_i18n: '',
			$t: '',
			userInfo: {},
			language: ''
		},
		onLaunch: function() {
			let context = this;
			this.globalData._i18n = this.$i18n //这是原型链上的$i18n  
			this.globalData.$t = function(str) {
				let $i18n = context.globalData.$i18n //这里去拿global的i18n  
				return context.$t(str)
			}
			this.getAreaSize()
			const SDKAppID = genTestUserSig('').sdkAppID;
			uni.$aegis.reportEvent({
				name: 'onLaunch',
				ext1: 'onLaunch-success',
				ext2: 'uniTuikitExternal',
				ext3: `${SDKAppID}`,
			})
			uni.setStorageSync(`TIM_${SDKAppID}_isTUIKit`, true);
			// 重点注意： 为了 uni-app 更好地接入使用 tim，快速定位和解决问题，请勿修改 uni.$TUIKit 命名。
			// 如果您已经接入 tim ，请将 uni.tim 修改为 uni.$TUIKit。
			uni.$TUIKit = TIM.create({
				SDKAppID: SDKAppID
			});
			// #ifndef H5
			uni.$TUIKit.registerPlugin({
				'cos-wx-sdk': COS
			});
			// #endif

			// #ifdef H5
			uni.$TUIKit.registerPlugin({
				'tim-upload-plugin': TIMUploadPlugin
			})
			// #endif
			// 将原生插件挂载在 uni 上
			// #ifdef APP-PLUS
			// uni.$TUICalling = TUICalling;
			// #endif
			// 如果您已创建了 tim，请将 tim 实例挂载在 wx 上，且不可以修改 wx.$TIM（修改变量可能导致 TUICalling 组件无法正常使用）, 完成 TUICalling 初始化，
			// 如果您没有创建，可以不传
			// #ifdef MP-WEIXIN
			wx.$TIM = uni.$TUIKit;
			// #endif
			uni.$TUIKitTIM = TIM;
			uni.$TUIKitEvent = TIM.EVENT;
			uni.$TUIKitVersion = TIM.VERSION;
			uni.$TUIKitTypes = TIM.TYPES; // 监听系统级事件
			uni.$resetLoginData = this.resetLoginData();
			uni.$TUIKit.on(uni.$TUIKitEvent.SDK_READY, this.onSDKReady);
			uni.$TUIKit.on(uni.$TUIKitEvent.SDK_NOT_READY, this.onSdkNotReady);
			uni.$TUIKit.on(uni.$TUIKitEvent.KICKED_OUT, this.onKickedOut);
			uni.$TUIKit.on(uni.$TUIKitEvent.ERROR, this.onTIMError);
			uni.$TUIKit.on(uni.$TUIKitEvent.NET_STATE_CHANGE, this.onNetStateChange);
			uni.$TUIKit.on(uni.$TUIKitEvent.SDK_RELOAD, this.onSDKReload);
			uni.$TUIKit.on(uni.$TUIKitEvent.MESSAGE_RECEIVED, this.onMessageReceived);
			uni.$on('TUIchatsetMessageRead', (msg) => {
				context.getImUnread()
			});
			const value = uni.getStorageSync('userinfo');
			// console.log(value)
			if (value) {
				const userSig = value.im.UserSig;
				const uiddd = value.im.userId;
				// console.log('uni.$TUIKitEvent.SDK_RELOAD, this.onSDKReload')
				uni.$TUIKit.login({
						userID: uiddd,
						userSig: userSig
					})
					.then(function(imResponse) {
						console.log('imResponse:', imResponse.data); // 登录成功
						if (imResponse.data.repeatLogin === true) {
							// 标识帐号已登录，本次登录操作为重复登录。v2.5.1 起支持
							console.log(imResponse.data.errorInfo);
						}
					})
					.catch(function(imError) {
						console.warn('login error:', imError); // 登录失败的相关信息
					});
			}
			// #ifdef APP-PLUS
			uni.onPushMessage((res) => {
				console.log(res)
				if (res.type == 'click') {
					// 'order_type' => 'store',//store(外卖订单)|run(跑腿订单)
					//        'notice_type' => 'chat',//chat(聊天)|system(系统通知)|mer_take_order(商家接单)|rider_take_order(骑手接单)|order_complete(订单完成)
					//        'store_order_id' => 0,//店铺订单id
					//        'run_order_id' => 0,//跑腿订单id
					//        'notice_id' => 0,//系统通知id
					//        'top_type_id' => 1,//店铺类型
					const value = uni.getStorageSync('userinfo');
					if (res.data.payload && value && value.id && value.token) {
						console.log(res.data.payload)
						const notice_type = res.data.payload.notice_type
						if (notice_type == 'system') {
							uni.navigateTo({
								url: 'package-message/pages/sysMsgDetail?id=' + res.data.payload.notice_id
							})
						} else if (notice_type == 'mer_take_order' || notice_type == 'order_complete' || notice_type == 'rider_take_order') {
							
							if (res.data.payload.order_type == 'store') {
								uni.navigateTo({
									url:'/package-index/pages/indexOrderDetail?id=' + res.data.payload
										.store_order_id
								})
							} else if (res.data.payload.order_type == 'run'){
								if (notice_type == 'rider_take_order'){
									uni.navigateTo({
										url:'package-publishOrder/pages/order-map-detaile?ID=' + es.data.payload
										.run_order_id
									})
								}else{
									uni.navigateTo({
										url:'package-publishOrder/pages/postOrderDetail?ID=' + es.data.payload
										.run_order_id
									})
								}
								
							}
						
						} 
					}
					
				}
			})
			// #endif
		},
		onShow: function() {
			const value = uni.getStorageSync('userinfo');
			console.log(value)
			console.log('App Show')
			// #ifdef APP-PLUS
			plus.runtime.setBadgeNumber(0, 0);
			// #endif

		},
		onHide: function() {
			console.log('App Hide')
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		methods: {
			getAreaSize(e) {

				var res = uni.getSystemInfoSync()
				this.globalData.windowHeight = res.windowHeight
				this.globalData.windowWidth = res.windowWidth
				this.globalData.screenHeight = res.screenHeight
				this.globalData.safeArea = res.safeArea
				this.globalData.top = res.safeArea.top
				this.globalData.safeAreaBottom = res.screenHeight - res.safeArea.bottom
				this.globalData.statusBarHeight = res.statusBarHeight
				this.globalData.pageHeight = this.globalData.windowHeight
				this.globalData.platform = res.platform
				this.globalData.navHeight = res.statusBarHeight + 44
				// console.log(this.platform);
				// #ifdef APP-PLUS
				// plus.runtime.getProperty(plus.runtime.appid, (wgtinfo) => {

				// 	this.version = wgtinfo.version; //应用版本号
				// })
				// #endif

				// #ifdef MP-WEIXIN
				// console.log('11111111121323123123133123123123131323212');
				this.globalData.safeAreaBottom = res.screenHeight - res.safeArea.bottom
				this.globalData.pageHeight = this.globalData.windowHeight + 44 + this.globalData.top
				var weixin = uni.getMenuButtonBoundingClientRect();
				this.globalData.top = weixin.top
				this.globalData.menuButtonWidth = weixin.width
				this.globalData.menuButtonLeft = weixin.left
				this.globalData.menuButtonHeight = weixin.height
				// #endif
				// this.getHomeConfig()

				uni.setTabBarItem({
					index: 0,
					text: this.i18n.tabBar.home
				});
				uni.setTabBarItem({
					index: 1,
					text: this.i18n.tabBar.order
				});
				uni.setTabBarItem({
					index: 2,
					text: this.i18n.tabBar.message
				});
				uni.setTabBarItem({
					index: 3,
					text: this.i18n.tabBar.shopManager
				});
				uni.setTabBarItem({
					index: 4,
					text: this.i18n.tabBar.mine
				});
			},
			// getHomeConfig() {
			// 	var that = this;
			// 	console.log(this.globalData.weburl)
			// 	uni.request({
			// 		url: this.globalData.weburl + 'api/?s=App.Home.GetConfig',
			// 		data: {},
			// 		success: function(res) {

			// 			if (res.data.ret == 200) {
			// 				if (res.data.data.code == 0) {
			// 					that.globalData.service_url_kefu = that.http.decypt(res.data.data.info[0]
			// 						.service_url)
			// 					console.log(that.globalData.service_url_kefu);
			// 				}
			// 			}

			// 		},
			// 		fail: function(error) {
			// 			console.log(error)
			// 			uni.hideLoading();

			// 		},
			// 		complete: function(res) {
			// 			console.log(res)
			// 		}
			// 	})
			// this.http.sendRequest('Home.GetConfig', {}).then(function(res) {
			// 	console.log(JSON.stringify(res))
			// 	if (res.code == 0) {
			// 		this.globalData.service_url_kefu = res.info[0].service_url
			// 	}
			// })
			// },
			// TODO:
			resetLoginData() {
				// this.globalData.expiresIn = '';
				// this.globalData.sessionID = '';
				// this.globalData.userInfo = {
				// 	userID: '',
				// 	userSig: '',
				// 	token: '',
				// 	phone: ''
				// };
				// this.globalData.userProfile = null;
				// logger.log(`| app |  resetLoginData | globalData: ${this.globalData}`);
			},
			onTIMError() {},
			onSDKReady({
				name
			}) {
				const isSDKReady = name === uni.$TUIKitEvent.SDK_READY ? true : false
				uni.$emit('isSDKReady', {
					isSDKReady: true
				});
			},
			onNetStateChange() {},
			onSDKReload() {},
			onSdkNotReady() {},
			onKickedOut() {
				uni.showToast({
					title: '您被踢下线',
					duration: 2000,
					icon: 'error'

				});
			}
		}
	}
</script>

